import { Component, OnInit } from '@angular/core';
import { SharedService } from '../../shared/services/shared.service';
import { environment } from '../../../environments/environment';


@Component({
  selector: 'app-my-strains-review',
  templateUrl: './my-strains-review.component.html',
  styleUrls: ['./my-strains-review.component.css']
})
export class MyStrainsReviewComponent implements OnInit {

	  public _host      = environment.config.BASE_URL;
    public dispensaries  = [];
    public object = {
  		product_id:'',///product_id
  		type:'product',
  		rating:null,
  		detail:''
  	}
    public rating= null;
    public existRating = false;

  constructor(
    	private _sharedService: SharedService) { }

    ngOnInit() {
  		this.fetch();
    }
  
    fetch() {
      this._sharedService.myStrains('',1,1000).then((res: any) => {
          if(res.success) {
              this.dispensaries = res.data; 
          } else {
          }
      },(error) => { });
    }

    clickStar( givenStar ) {
        this.rating = givenStar;
        this.object.rating = this.rating;
    }


    save() {
      this._sharedService.loader('show');
	   this._sharedService.post(this.object,'reviews').then((res: any) => {
       this._sharedService.loader('hide');
            if(res.success) {
                this.object = {
                  product_id:'',
                  type:'dispensary',
                  rating:null,
                  detail:''
                }
            } else {
            }
        },(error) => { });
    }
}



