import { Component, OnInit, ViewChild } from '@angular/core';
import { SharedService } from '../../shared/services/shared.service';
import { environment } from '../../../environments/environment';
// import { NgxSpinnerService } from 'ngx-spinner';
import { ImageResult } from 'ng2-imageupload';

@Component({
  selector: 'app-edit-profile',
  templateUrl: './edit-profile.component.html',
  styleUrls: ['./edit-profile.component.css']
})
export class EditProfileComponent implements OnInit {
  @ViewChild('myInput')
      myInputVariable: any;

public _host      = environment.config.BASE_URL;
	public user        = {
		day:'',
		month:'',
		year:'',
		gender:'',
    firstName:'',
    lastName:'',
    email:'',
    image:''
	};
	public errMessage = '';
	public days = [];
	public month = [];
	public year = [];
	
    constructor(
      // private spinner: NgxSpinnerService, 
    	private _sharedService: SharedService) { }

    ngOnInit() {
  		this.fetch();
  		for (var i = 1; i <= 31; ++i) {
  			this.days.push(i);
  			if(i <=12)this.month.push(i);
  		}
  		for (var i = 1901; i < 2018; ++i) {
  			this.year.push(i)
  		}
    }
  
    fetch() {
        this._sharedService.loader('show');
        this._sharedService.get('getuserdetail').then((res: any) => {
            this._sharedService.loader('hide');
            if(res.success) {
                this.user = res.data.user; 
                if(!this.user.day) this.user.day = '';
                if(!this.user.month) this.user.month = '';
                if(!this.user.year) this.user.year = '';
                if(!this.user.gender) this.user.gender = '';
            } else {
            }
        },(error) => { });
    }

    save() {
       // this.spinner.show();
      this._sharedService.loader('show');
		  let data = JSON.parse(JSON.stringify(this.user));
        this._sharedService.put(data,'updateprofile').then((res: any) => {
            this._sharedService.loader('hide');
                 // this.spinner.hide();
        },(error) => { });
    }

    uploadImage(imageResult: ImageResult) {
        let object = {
            data:imageResult.dataURL,
            type:'dispensary'
        }
        this.myInputVariable.nativeElement.value = "";
        this._sharedService.loader('show');
        this._sharedService.post(object,'upload').then( res => {
            this._sharedService.loader('hide');
            if(res.success) {
                this.user.image = res.data.fullPath;
            } else {
            }
        },err => { });
    }

}



