import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';

import { DashboardRoutingModule } from './dashboard-routing.module';
import { SharedModule } from '../shared/shared.module';
import { MyDispensariesComponent } from './my-dispensaries/my-dispensaries.component';
import { MyStrainsComponent } from './my-strains/my-strains.component';
import { MyDispensaryReviewComponent } from './my-dispensary-review/my-dispensary-review.component';
import { MyStrainsReviewComponent } from './my-strains-review/my-strains-review.component';
import { MyAccountInfoComponent } from './my-account-info/my-account-info.component';
import { EditProfileComponent } from './edit-profile/edit-profile.component';
import { NgxSpinnerModule } from 'ngx-spinner';
import { ImageUploadModule } from 'ng2-imageupload';

@NgModule({
  imports: [
    CommonModule,
    SharedModule,
    FormsModule,
    NgxSpinnerModule,
    DashboardRoutingModule,
    ImageUploadModule
  ],
  declarations: [MyDispensariesComponent, MyStrainsComponent, MyDispensaryReviewComponent, MyStrainsReviewComponent, MyAccountInfoComponent, EditProfileComponent]
})
export class DashboardModule { }



