import { Component, OnInit } from '@angular/core';
import { SharedService } from '../../shared/services/shared.service';
import { environment } from '../../../environments/environment';

@Component({
  selector: 'app-my-dispensaries',
  templateUrl: './my-dispensaries.component.html',
  styleUrls: ['./my-dispensaries.component.css']
})
export class MyDispensariesComponent implements OnInit {
	public _host      = environment.config.BASE_URL;
	public arrayObj   = [];
	public page 	  = 1;
	public count	  = 100;
	public search 	  = ''

    constructor(
    	private _sharedService: SharedService) { }

    ngOnInit() {
  		this.fetch();
    }
  
    fetch() {
        this._sharedService.myDispensary(this.search,this.page,this.count).then((res: any) => {
            if(res.success) {
                this.arrayObj = res.data.productList; 
            } else {
            }
        },(error) => { });
    }

    unfollow(id,index) {
      if(confirm("Do you want to Unfollow?")) {
          let obj = {
            item_id:id,
            type:'dispensary'
          }
          this._sharedService.post(obj,'favourite').then((res: any) => {
              if(res.success) {
                  this.arrayObj.splice(index);
              } else {
              }
          },(error) => { });
      }
    }

}



