import { Component, OnInit } from '@angular/core';
import { SharedService } from '../../shared/services/shared.service';
import { environment } from '../../../environments/environment';

@Component({
  selector: 'app-my-dispensary-review',
  templateUrl: './my-dispensary-review.component.html',
  styleUrls: ['./my-dispensary-review.component.css']
})
export class MyDispensaryReviewComponent implements OnInit {

      public _host      = environment.config.BASE_URL;
      public dispensaries  = [];
      public object = {
      	item_id:'',///product_id
      	type:'dispensary',
      	rating:null,
      	detail:''
      }
      public rating= null;
      public existRating = false;

      // type = product/dispensary/brand/doctor
    constructor(
    	private _sharedService: SharedService) { }

    ngOnInit() {
  		this.fetch();
    }
  
    fetch() {

	   this._sharedService.get('alldespensary').then((res: any) => {
            if(res.success) {
                this.dispensaries = res.data.dispensaryList; 
            } else {
            }
        },(error) => { });
    }

    clickStar( givenStar ) {
        this.rating = givenStar;
        this.object.rating = this.rating;
    }


    save() {
      this._sharedService.loader('show');
	   this._sharedService.post(this.object,'reviews').then((res: any) => {
       this._sharedService.loader('hide');
            if(res.success) {
                this.object = {
                  item_id:'',
                  type:'dispensary',
                  rating:null,
                  detail:''
                }
            } else {
            }
        },(error) => { });
    }

}



