import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { MyDispensariesComponent } from './my-dispensaries/my-dispensaries.component';
import { MyStrainsComponent } from './my-strains/my-strains.component';
import { MyDispensaryReviewComponent } from './my-dispensary-review/my-dispensary-review.component';
import { MyStrainsReviewComponent } from './my-strains-review/my-strains-review.component';
import { MyAccountInfoComponent } from './my-account-info/my-account-info.component';
import { EditProfileComponent } from './edit-profile/edit-profile.component';

const routes: Routes = [{
		path: '',
		component: MyDispensariesComponent
	},{
		path: 'my-strains',
		component: MyStrainsComponent
	},{
		path: 'my-dispensary-review',
		component: MyDispensaryReviewComponent
	},{
		path: 'my-strains-review',
		component: MyStrainsReviewComponent
	},{
		path: 'my-account-info',
		component: MyAccountInfoComponent
	},{
		path: 'edit-profile',
		component: EditProfileComponent
	}];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class DashboardRoutingModule { }



